import React from "react";
import PropTypes from "prop-types";
import {
  ButtonContainer,
  CartSummaryContainer,
  SubmitButton,
  Subtotal,
  OrderTotalPrice,
} from "./style";

export function CartSummary({ totalPrice }) {
  return (
    <CartSummaryContainer>
      <h2></h2>
      <Subtotal>
        <div></div>
        <div>NT$ {totalPrice}</div>
      </Subtotal>
      <Subtotal>
        <div></div>
        <div>NT$ 0</div>
      </Subtotal>
      <OrderTotalPrice>
        <b>NT$ {totalPrice}</b>
      </OrderTotalPrice>
      <ButtonContainer>
        <SubmitButton to="/checkout"></SubmitButton>
      </ButtonContainer>
    </CartSummaryContainer>
  );
}

CartSummary.propTypes = {
  totalPrice: PropTypes.number,
};
