import {
  TabOrderGroup,
  TabOrderItem,
  TabOrderTop,
  TabOrderCenter,
  TabOrderProductTitle,
  TabOrderProduct,
  TabOrderProductImg,
  TabOrderProductTotal,
  TabOrderBottom,
  OrderStatus,
  IsDoneLabel,
  IsPaidLabel,
  IsSentLabel,
} from "./style";
import { Link } from "react-router-dom";

export default function TabOrder({ order, orderItems }) {
  const options = {
    // day: "numeric", // (e.g., 1)
    // month: "short", // (e.g., Oct)
    // year: "numeric", // (e.g., 2019)
    hour: "2-digit", // (e.g., 02)
    minute: "2-digit", // (e.g., 02)
    hour12: true, // 24 
    timeZone: "Asia/Taipei", // "America/New_York"
  };
  return (
    <TabOrderGroup>
      <TabOrderItem>
        <TabOrderTop>
          
          {new Date(order.createdAt).toLocaleDateString("zh-TW", options)}
          <br />
          {order.order_number}
          <br />
          <OrderStatus>
            
            <IsDoneLabel isCancel={order.is_cancel} isDone={order.is_done}>
              {(order.is_cancel && "") ||
                (order.is_done ? "" : "")}
            </IsDoneLabel>
            <IsPaidLabel isPaid={order.is_paid}>
              {order.is_paid ? "" : ""}
            </IsPaidLabel>
            <IsSentLabel isSent={order.is_sent}>
              {order.is_sent ? "" : ""}
            </IsSentLabel>
          </OrderStatus>
        </TabOrderTop>
        <TabOrderCenter>
          <TabOrderProductTitle></TabOrderProductTitle>
          {orderItems.map((orderItems) => (
            <TabOrderProduct
              orderItems={orderItems}
              key={orderItems.product_id}
              id="orderProduct"
            >
              <TabOrderProductImg>
                <Link to={"/product/" + orderItems.product_id} target="_blank">
                  <img
                    src={orderItems.product_image}
                    alt={orderItems.product_name}
                  />
                </Link>
              </TabOrderProductImg>
              <b>
                <Link to={"/product/" + orderItems.product_id} target="_blank">
                  {orderItems.product_name}
                  {orderItems.product_feature}
                </Link>
              </b>
              &emsp; NT$ {orderItems.product_price}*
              {orderItems.product_quantity}=
              {orderItems.product_price * orderItems.product_quantity}
            </TabOrderProduct>
          ))}
          <TabOrderProductTotal id="totalPrize">
            <b>NT$ {order.total}</b>
          </TabOrderProductTotal>
        </TabOrderCenter>
        <TabOrderBottom>
          {order.buyer_fullname}
          <br />
          {order.buyer_phone}
          <br />
          {order.postal_code}
          {order.buyer_address}
        </TabOrderBottom>
      </TabOrderItem>
    </TabOrderGroup>
  );
}
